@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
header {
	width: 100vw;
	margin-bottom: 15px;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
}

.active_nav a {
	color: rgb(0, 140, 255) !important;
}

@media (max-width: 600px) {
	.first_nav_element {
		padding-left: 0;
	}
	.__nav {
		padding-left: 0 !important;
	}
}

.__nav {
	margin-top: 30px;
	list-style-type: none;
}

.__nav li {
	display: inline-block;
	padding: 0 20px;
}

.__nav li a {
	color: #fff;
	text-decoration: none;
	transition: all 0.3s ease 0s;
}

.__nav li a:hover {
	color: rgb(0, 140, 255);
}


.toggle_button .bar {
	height: 3px;
	width: 100%;
	background-color: #fff;
	border-radius: 10px;
}

.toggle_button {
	position: absolute;
	top: .75rem;
	right: 1rem;
	display: none;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	width: 30px;
	height: 21px;
}

@media (max-width: 400px) {
	.toggle_button {
		display: -webkit-flex;
		display: flex;
	}

	.__nav {
		/* display: none; */
		display: none;
		-webkit-flex-direction: column;
		        flex-direction: column;
		position: absolute;
		top: .5rem;
		left: .75rem;
	}

	.visible_navbar {
		display: -webkit-flex;
		display: flex;
	}

	.__nav li {
		padding: 10px;
		font-size: 1.5rem;
	}

	.first_nav_element {
		padding: inherit;
	}


}

.footer_links {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: center;
	        justify-content: center;
}

.footer_border {
	margin: auto;
	width: 97vw;
	border-radius: 50px;
	background-color: #fff;
	text-align: center;
}

.footer_links a {
	font-size: 1.5rem;
	opacity: .5;
	margin: 0 5px;
	color: inherit;
	text-decoration: none;
	transition: opacity ease-in 0.3s;
}

.footer_links a:hover {
	cursor: pointer;
	opacity: 1;
}

.copyright {
	font-family: 'Source Code Pro', monospace;
}

body {
	box-sizing: border-box;
	background-color: rgb(39, 39, 39);
	color: #fff;
	font-family: Quicksand;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin: 0;
	padding: 0;
}

